<template>
    <div>
    <!-- App Toolbar -->
    <app-toolbar></app-toolbar>

    <!--Contenido-->
    <v-content >

        <v-container fluid>

            <v-row no-gutters class="title-banner despacho-barrio"></v-row>

            <v-row no-gutters >
                <v-col md="3" sm="12" xs="12">  
                    <v-img :src="logo" aspect-ratio="2" height="320"></v-img>
                </v-col>
                <v-col md="9" sm="12" xs="12" class="">  
                    <v-carousel
                        cycle
                        height="320"
                        hide-delimiter-background
                        show-arrows-on-hover
                    >
                        <v-carousel-item
                            v-for="(slide, i) in slides"
                            :key="i"
                            :src="slide"
                            cover
                        >
                        </v-carousel-item>
                    </v-carousel>
                </v-col>
            </v-row>

            <v-row no-gutters class="title-banner despacho-cali"></v-row>

            <v-row>

                <v-col>
    
    <v-sheet elevation="4">

    <v-slide-group v-model="categoria" class="pa-4" show-arrows center-active>
      
      <v-slide-item v-for="(item, i) in categorias" :key="i"  v-slot:default="{ active, toggle }" :value="item.id">
        
        <v-card
          :color="active ? 'primary' : 'grey lighten-1'"
          class="ma-3"
          height="200"
          width="200"
          @click="toggle"
        >

            <v-img class="white--text align-end" height="200px" :src="item.src" align="center" justify="center">
                <v-scale-transition>
                <v-icon v-if="active" color="white" size="48">mdi-close-circle-outline</v-icon>
                </v-scale-transition>
            </v-img> 

        </v-card>

      </v-slide-item>

    </v-slide-group>

    <v-expand-transition>
      
      <v-sheet v-if="categoria != null" color="grey lighten-4" min-height="400" tile>

            <v-row align="center" justify="center">
                <v-chip close dark color="purple" class="my-3" @click:close="categoria = null">
                    <v-avatar left>
                        <v-icon class="mx-2">{{ getCategoria(categoria).icon }}</v-icon>
                    </v-avatar>
                    Categoria {{categoria}}  {{ getCategoria(categoria).nb_categoria }} 
                </v-chip>     
            </v-row>

            <v-fade-transition>
            <list-comercio :filtros="{ nombre: null, categoria: categoria }"></list-comercio>
            </v-fade-transition>

      </v-sheet>

    </v-expand-transition>
  </v-sheet>
                        </v-col>

                    </v-row>

        </v-container>
    </v-content>

    <v-navigation-drawer
      v-model="drawerRight"
      app
      clipped
      right
       class="grey lighten-5"   
    >
        <app-filters></app-filters> 

    </v-navigation-drawer>


    <!--Mensaje Snack Bar-->
    <app-message></app-message>

    </div>

</template>
<script>

import AppMenu from  '~/components/app/AppMenu';
import AppToolbar from '~/layouts/AppToolbar';
import AppFilters from '~/components/app/AppFilters';
import ListComercio from '~/components/comercio/ListComercio';

export default 
{
    components: { 
        'app-menu': AppMenu,
        'app-toolbar': AppToolbar, 
        'app-filters': AppFilters,
        'list-comercio': ListComercio,
    },
    computed: {

        categoria:
        {
            get() {
                return this.$store.getters['getCategoria']
            },
            set(categoria) {
                this.$store.commit('setCategoria', categoria )
            }
        }
           
    },

    data () 
	{
        return {
            logo: require('~/assets/img/logo.jpg'),
            clipped:     false,
            drawer:      true,
            drawerRight: true,
            fixed:       false,
            items:       [],
            miniVariant: false,
            right: true,
            slides: [
                        { src: require('~/assets/img/bebidas.jpg') },
                        { src: require('~/assets/img/bebidas2.jpg') },
                        { src: require('~/assets/img/plantas.jpg') }
                    ],
            categorias: [
                { id: 1 , nb_categoria: 'Ropa', icon: 'mdi-tshirt-crew' , src: require('~/assets/img/categorias/ropa.jpg') },
                { id: 2 , nb_categoria: 'Calzado', icon: 'mdi-shoe-formal' , src: require('~/assets/img/categorias/calzado.jpg') },
                { id: 3 , nb_categoria: 'Comida', icon: 'mdi-food' , src: require('~/assets/img/categorias/comida.jpg') },
                { id: 4 , nb_categoria: 'Belleza', icon: 'mdi-hair-dryer' , src: require('~/assets/img/categorias/belleza.jpg') },
                { id: 5 , nb_categoria: 'Mercado', icon: 'mdi-cart' , src: require('~/assets/img/categorias/mercado.jpg') },
                { id: 6 , nb_categoria: 'Mensajeria', icon: 'mdi-motorbike' , src: require('~/assets/img/categorias/mensajeria.jpg') },
                { id: 7 , nb_categoria: 'Medicinas', icon: 'mdi-needle' , src: require('~/assets/img/categorias/medicinas.jpg') },
                { id: 8 , nb_categoria: 'Ferreteria', icon: 'mdi-hammer-screwdriver' , src: require('~/assets/img/categorias/ferreteria.jpg') }
            ],
        }
    },
    methods: {
        getCategoria(id)
        {
            return this.categorias.filter( categoria => categoria.id == id )[0];
        }
    }
}
</script>

<style>
.v-slide-group__next, .v-slide-group__prev{
  min-width: 1px !important;
}
.title-banner {
    background-repeat: no-repeat;
    background-color: white;
    background-size: contain; 
    min-height: 100px;
}
.despacho-barrio {
    background-image: url('/images/despacho-barrio.png'); 
}

.despacho-cali {
    background-image: url('/images/despacho-cali.png'); 
}
</style>
